.p-faq {
  background-color: var(--color-black-2);

  &-main {
    padding-top: 210px;

    @include s-1440 {
      padding-top: 110px;
    }

    @include s-640 {
      padding-top: 0;
    }
  }

  & h1 {
    text-align: center;
    margin: 206px 0 238px 0;

    @include s-1920 {
      margin: 118px 0 146px 0;
    }

    @include s-1440 {
      margin: 118px 0 122px 0;
    }

    @include s-640 {
      margin: 154px 20px 76px 20px;
    }
  }

  & .faq {
    max-width: 880px;
    width: 100%;
    margin: 0 auto 150px auto;
    list-style-type: none;

    @include s-1920 {
      max-width: 760px;
      margin: 0 auto 70px auto;
    }

    @include s-1440 {
      max-width: 600px;
      margin: 0 auto 60px auto;
    }

    @include s-640 {
      padding: 0 20px;
      margin: 0 auto 40px auto;
    }

    &-el {
      padding: 40px;
      border: 1px solid var(--color-grey-2);
      margin: 20px 0;
      transition: all ease-out 0.3s;

      @include s-1440 {
        padding: 30px 40px;
      }

      @include s-640 {
        padding: 15px;
      }

      &.active {
        border-width: 1px;
        border-style: solid;
        border-image: linear-gradient(to right bottom, #A377FF, #30C9CB);
        border-image-slice: 1;
      }

      &-top {
        display: flex;
        justify-content: flex-start;
        align-items: center;

        & .icon {
          width: 24px;
          height: 24px;
          margin-right: 30px;
          position: relative;

          @include s-640 {
            width: 16px;
            height: 16px;
            margin-right: 10px;
            min-width: 16px;
          }

          &--line {
            width: 100%;
            height: 2px;
            display: block;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            background-color: var(--color-white);

            &:nth-child(2) {
              transform: translate(-50%, -50%) rotate(90deg);
            }
          }
        }

        & h6 {
          line-height: 120%;
        }
      }

      &-hidden {
        display: none;
      }
    }
  }
}
