.c-footer {
  background-color: var(--color-black-2);
  padding: 66px 80px 0;
  margin: auto 0 0 0;

  &-top {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    padding-bottom: 70px;

    &--logo {
      & .logo {
        display: inline-block;
        max-width: 138px;

        &--text {
          display: block;
          font-size: 16px;
          color: var(--color-grey);
          margin-top: 10px;
        }
      }

      & img {
        width: 100%;
        height: auto;
      }
    }

    &-menu {
      display: flex;
      justify-content: space-between;
      align-items: stretch;
      max-width: 896px;
      width: 100%;

      & .menu {
        margin-left: 15px;

        &:last-of-type {
          margin-left: 0;
        }

        &-list {
          list-style-type: none;

          &-item {
            margin-bottom: 20px;

            &:last-of-type {
              margin-bottom: 0;
            }

            & a {
              font-size: 18px;
              line-height: 150%;
              letter-spacing: 0.02em;
              color: var(--color-white-2);
            }
          }
        }
      }
    }
  }

  &-bottom {
    font-size: 16px;
    line-height: 150%;
    color: var(--color-white-2);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0;
    border-top: solid 1px var(--color-white-2);

    & .social {
      list-style-type: none;
      display: flex;
      justify-content: flex-start;
      align-items: center;

      & li {
        margin-right: 30px;

        &:last-of-type {
          margin-right: 0;
        }
      }

      & a {
        display: flex;
        justify-content: center;
        align-items: center;
      }

      & svg {
        width: 24px;
        height: 24px;
      }
    }

    & .publish {
    }

    & .development {
    }
  }
}
